import React from 'react';
import styled from "styled-components";
import Fade from 'react-reveal/Fade';
import picture from "../images/pictures/leaf.png";
import photo from "../images/photos/photo1.png";

const ContentContainer = styled.div`
    padding-top: 15%;
    padding-bottom: 15%;
`;

const TitleDiv = styled.div`
    padding-bottom: 10%;
    font-family: 'NanumSquareacB';
    font-size: 100%;
`;

const BodyDiv = styled.div`
    padding: 2%;
`;

const StyledSpan = styled.span`
    margin-left:2%;
    font-family: 'NanumSquareAceb';
`;

const Picture = styled.img`
    width: 80%;
    padding-bottom: 5%;
    padding-top: 5%;
`;

const Photo = styled.img`
    width: 100%;
    padding-top: 10%;
`;


export default function ContentsContainer() {
    return (<div>
            <ContentContainer>
                <Fade bottom>
                    <Picture src={picture}/>
                </Fade>

                <Fade bottom cascade>
                    <TitleDiv>.</TitleDiv>
                    <BodyDiv>9  </BodyDiv>
                    <BodyDiv>.</BodyDiv>
                    <BodyDiv>  </BodyDiv>
                    <BodyDiv> .</BodyDiv>
                </Fade>
                <Fade bottom>
                    <Picture src={picture}/>
                </Fade>
                <Fade bottom>
                    <Photo src={photo}/>
                </Fade>
                <Fade bottom cascade>
                    <div className="nameSection">
                        <div className="nameDiv"><span>  </span><span className="smallFont"> </span><StyledSpan></StyledSpan></div>
                        <div className="nameDiv"><span>  </span><span className="smallFont"> </span><StyledSpan></StyledSpan></div>
                    </div>
                </Fade>
            </ContentContainer>
        </div>
    )
        ;
}